:root {
  --main-color: #007a7e;
  --accent: #00a7e7;
  --first-color: #005a70;
  --second-color: #ed174a;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

html {
  box-sizing: border-box;
}

*, :after, :before {
  box-sizing: inherit;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

.list {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.link {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

address {
  font-style: normal;
}

body {
  color: var(--main-color);
  background-color: var(--first-color);
  letter-spacing: .03em;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  position: relative;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

@media screen and (min-width: 320px) and (max-width: 1199px) {
  .section {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

@media screen and (min-width: 1200px) {
  .section {
    padding-top: 94px;
    padding-bottom: 94px;
  }
}

.backdrop {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, .2);
  transition: opacity .25s linear, visibility .25s linear;
  position: fixed;
  top: 0;
}

.modal {
  min-height: 581px;
  background: var(--first-color);
  border-radius: 4px;
  padding: 40px;
  transition: transform .25s linear;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scaleY(1);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

@media screen and (min-width: 320px) {
  .modal {
    width: 100%;
  }
}

@media screen and (min-width: 480px) {
  .modal {
    width: 450px;
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    width: 528px;
  }
}

.backdrop.is-hidden.modal {
  transform: translate(-50%, -50%)scaleY(.7);
}

.modal-form {
  z-index: 1;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%)scaleY(1);
}

.form-text {
  text-align: center;
  letter-spacing: .03em;
  color: var(--main-color);
  margin-bottom: 12px;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.15;
}

.form-label {
  letter-spacing: .01em;
  color: var(--second-color);
  flex-direction: column;
  margin-bottom: 4px;
  font-size: 12px;
  line-height: 1.16;
  display: block;
}

.modal-field {
  margin-bottom: 10px;
}

.input-wrap {
  position: relative;
}

.form-input {
  width: 100%;
  height: 40px;
  cursor: pointer;
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  outline: rgba(0, 0, 0, 0);
  padding-left: 42px;
  transition: border .25s cubic-bezier(.4, 0, .2, 1);
}

.form-input:focus {
  border: 1px solid var(--accent);
}

.modal-icon {
  margin-left: 11px;
  position: absolute;
  top: 50%;
  left: 5px;
  transform: translateY(-50%);
}

.form-input:focus + .modal-icon {
  fill: var(--accent);
}

.form-textarea {
  width: 100%;
  height: 120px;
  letter-spacing: .01em;
  color: var(--main-color);
  cursor: pointer;
  resize: none;
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  margin-bottom: 20px;
  padding: 12px 16px;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
  transition: border .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.form-textarea::-ms-input-placeholder {
  letter-spacing: .01em;
  color: rgba(117, 117, 117, .5);
  font-size: 12px;
  font-weight: 400;
  line-height: 1.16;
}

.form-textarea::placeholder {
  letter-spacing: .01em;
  color: rgba(117, 117, 117, .5);
  font-size: 12px;
  font-weight: 400;
  line-height: 1.16;
}

.form-textarea:focus {
  border: 1px solid var(--accent);
  outline: rgba(0, 0, 0, 0);
}

.modal-privacy {
  justify-content: center;
  margin-bottom: 30px;
  display: flex;
}

.modal-text {
  letter-spacing: .03em;
  color: #757575;
  align-items: center;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.71;
  display: flex;
}

@media screen and (min-width: 480px) {
  .modal-text {
    font-size: 12px;
  }
}

.modal-text .modal-check {
  width: 16px;
  height: 15px;
  border: 2px solid var(--main-color);
  fill: rgba(0, 0, 0, 0);
  border-radius: 2px;
  justify-content: center;
  align-items: center;
  margin-right: 8px;
  display: flex;
}

.input-check:checked + .modal-text .modal-check {
  background-color: var(--accent);
  fill: var(--first-color);
  border-color: rgba(0, 0, 0, 0);
}

.text-privacy {
  letter-spacing: .03em;
  color: var(--accent);
  margin-left: 7px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.71;
  -webkit-text-decoration-line: underline;
  text-decoration-line: underline;
}

@media screen and (min-width: 480px) {
  .text-privacy {
    font-size: 12px;
  }
}

.modal-form-button {
  width: 200px;
  height: 50px;
  background: var(--accent);
  width: 200px;
  height: 50px;
  color: var(--first-color);
  cursor: pointer;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  margin: auto;
  display: flex;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.modal-button {
  width: 30px;
  height: 30px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(0, 0, 0, .1);
